<template>
<div class="stock-page-content">
  <div class="header-content">
    <page-title :title="pageName"></page-title>
  </div>
  <div class="main-content">
    <div class="block-content-table">
      <min-title nameVal="库存分析-店铺"  :show="'3'"></min-title>
      <table-list :tableData="shopKcfxTableData" :key="'shopkcTable'" :type="'stock-shops-kcfx'" :needClick="false" :defaultSelected="true"
                  @changeTableOrder="changeTableOrder" ></table-list>
    </div>
    <div class="block-content-chart">
      <min-title :nameVal="shopQsfxTitle"  :show="'3'"></min-title>
      <div class="block-chart">
        <BarLineChart :chart-data="shopQsfxLineData" :option="getQsfxLineOption(shopQsfxLineData)"></BarLineChart>
      </div>
    </div>
    <div class="block-content-bigChart">
      <min-title :nameVal="shopJgfxTitle" :show="'3'" ></min-title>
      <type-list :tabsData="jgfxTypeList" :switchType="'stock-shops-jgfx'"  @activeTypeName = changeJgfxType ></type-list>
      <div class="switch-btn">
        <div class="switch-btn-core">
          <tabs :tabsData="blockSwitchList" :key="'stock-shops-jgfx'" @tabsType="tabsType" :switchType="'stock-shops-jgfx'"></tabs>
        </div>
      </div>
      <div class="block-chart">
        <bar-line-chart :chart-data="shopJgfxBarLineData" :option="getJgfxBarLineOption(shopJgfxBarLineData)"></bar-line-chart>
      </div>
    </div>
    <div class="block-content-table">
      <table-list :tableData="shopJgfxTableData" :type="'stock-shops-jgfx'"
                  @changeTableOrder="changeTableOrder"></table-list>
    </div>
<!--    <div class="block-content-table">-->
<!--      <min-title :nameVal="shopXsfxTitle" :tabsData="blockSwitchList_Xsfx" :show="'1'" :type="'stock-xsfx'" @tabsType="tabsType" ></min-title>-->
<!--      <table-list :tableData="xsfxTableData" :type="'stock-xsfx'"-->
<!--                  @changeTableOrder="changeTableOrder"></table-list>-->
<!--    </div>-->
  </div>
</div>
</template>

<script>
import pageTitle from '../../components/common/pageTitle'
import MinTitle from '../../components/common/minTitle'
import BarLineChart from '../../components/echarts/BarLineChart'
import TableList from '../../components/common/tableList'
import TypeList from '../../components/common/typeList'
import Tabs from '../../components/common/tabs'
import _ from 'lodash'
import {px2px} from '../../assets/js/lib/common/flexible' // 在echarts的option配置中，用到的字体大小，柱图宽度等数字时，要用此方法包起来
import {compareSort, initNum, formatNumber, switchPercent} from '../../assets/js/core/weapon' // 引入排序方法
import apiService from '../../assets/js/api'
export default {
  name: '',
  data () {
    return {
      pageName: '库存(万)',
      shopKcfxTableDataInit: [],
      shopKcfxTableData: {
        tableHeader: [
          {name: '店铺', prop: 'shop', needOrder: false, order: '0', isPresent: false, needArrow: false},
          {name: '控制差异率', prop: 'kzcy', needOrder: true, order: '0', isPresent: true, needArrow: true, newLine: true},
          {name: '差异率排名', prop: 'kzcypm', needOrder: true, order: '1', isPresent: false, needArrow: false, newLine: true},
          {name: '周转天数', prop: 'zzts', needOrder: true, order: '0', isPresent: false, needArrow: false, newLine: true},
          {name: '周转天数排名', prop: 'zztspm', needOrder: true, order: '0', isPresent: false, needArrow: false, newLine: true}
        ],
        tableContent: [
          // {dp: '水星一店', zzts: '200', zztspm: '1'},
          // {dp: '水星2店', zzts: '220', zztspm: '2'},
          // {dp: '水星3店', zzts: '230', zztspm: '3'},
          // {dp: '水星4店', zzts: '240', zztspm: '4'}
        ]
      },
      shopQsfxTitle: '趋势分析',
      shopJgfxTitle: '结构分析',
      shopXsfxTitle: '有库存无销售分析',
      blockSwitchList: [{name: '数量', value: '数量'}, {name: '金额', value: '金额'}],
      blockSwitchList_Xsfx: [{name: '7天', value: '7天'}, {name: '30天', value: '30天'}],
      shopQsfxLineData: [ // 趋势分析-店铺模拟的柱线图数据
        // {label: '1', zzts: '10', kzcy: '5', cy: '28'},
        // {label: '2', zzts: '20', kzcy: '15', cy: '34'},
        // {label: '3', zzts: '30', kzcy: '27', cy: '56'},
        // {label: '4', zzts: '40', kzcy: '38', cy: '34'},
        // {label: '5', zzts: '50', kzcy: '38', cy: '67'},
        // {label: '6', zzts: '55', kzcy: '48', cy: '23'}
      ],
      jgfxTypeList: [{name: '大类', value: '大类'}, {name: 'SPU', value: 'SPU'}],
      dmTableHeader: [{name: '大类', prop: 'type', needOrder: false, order: '0', isPresent: false, needArrow: false},
        {name: '库存金额', prop: 'ke', needOrder: true, order: '2', isPresent: false, needArrow: false, newLine: true},
        {name: '库存占比', prop: 'kb', needOrder: true, order: '0', isPresent: true, needArrow: false, newLine: true},
        {name: '销售金额', prop: 'xe', needOrder: true, order: '0', isPresent: false, needArrow: false, newLine: true},
        {name: '销售占比', prop: 'xb', needOrder: true, order: '0', isPresent: true, needArrow: false, newLine: true},
        {name: '偏差', prop: 'pc', needOrder: true, order: '0', isPresent: true, needArrow: true}],
      spumTableHeader: [{name: 'SPU', prop: 'type', needOrder: false, order: '0', isPresent: false, needArrow: false},
        {name: '库存金额', prop: 'ke', needOrder: true, order: '2', isPresent: false, needArrow: false, newLine: true},
        {name: '库存占比', prop: 'kb', needOrder: true, order: '0', isPresent: true, needArrow: false, newLine: true},
        {name: '销售金额', prop: 'xe', needOrder: true, order: '0', isPresent: false, needArrow: false, newLine: true},
        {name: '销售占比', prop: 'xb', needOrder: true, order: '0', isPresent: true, needArrow: false, newLine: true},
        {name: '偏差', prop: 'pc', needOrder: true, order: '0', isPresent: true, needArrow: true}],
      dqTableHeader: [{name: '大类', prop: 'type', needOrder: false, order: '0', isPresent: false, needArrow: false},
        {name: '库存数量', prop: 'ke', needOrder: true, order: '2', isPresent: false, needArrow: false, newLine: true},
        {name: '库存占比', prop: 'kb', needOrder: true, order: '0', isPresent: true, needArrow: false, newLine: true},
        {name: '销售数量', prop: 'xe', needOrder: true, order: '0', isPresent: false, needArrow: false, newLine: true},
        {name: '销售占比', prop: 'xb', needOrder: true, order: '0', isPresent: true, needArrow: false, newLine: true},
        {name: '偏差', prop: 'pc', needOrder: true, order: '0', isPresent: true, needArrow: true}],
      spuqTableHeader: [{name: 'SPU', prop: 'type', needOrder: false, order: '0', isPresent: false, needArrow: false},
        {name: '库存数量', prop: 'ke', needOrder: true, order: '2', isPresent: false, needArrow: false, newLine: true},
        {name: '库存占比', prop: 'kb', needOrder: true, order: '0', isPresent: true, needArrow: false, newLine: true},
        {name: '销售数量', prop: 'xe', needOrder: true, order: '0', isPresent: false, needArrow: false, newLine: true},
        {name: '销售占比', prop: 'xb', needOrder: true, order: '0', isPresent: true, needArrow: false, newLine: true},
        {name: '偏差', prop: 'pc', needOrder: true, order: '0', isPresent: true, needArrow: true}],
      shopJgfxTableDataInit: [],
      shopJgfxBarLineData: [],
      shopJgfxTableData: {
        tableHeader: [],
        tableContent: []
      },
      shopJgfxIndex: '00',
      shopJgfxTabIndex: 0,
      shopJgfxBtnIndex: 0
      // xsfxTableData: {
      //   tableHeader: [
      //     {name: 'SKU', prop: 'sku', needOrder: false, order: '0', isPresent: false, needArrow: false},
      //     {name: '销售数量', prop: 'xssl', needOrder: true, order: '1', isPresent: false, needArrow: false},
      //     {name: '库存数量', prop: 'kcsl', needOrder: true, order: '0', isPresent: false, needArrow: false},
      //     {name: '库存金额', prop: 'kcje', needOrder: true, order: '0', isPresent: true, needArrow: false},
      //     {name: '库存占比', prop: 'kczb', needOrder: true, order: '0', isPresent: true, needArrow: false}
      //   ],
      //   tableContent: [
      //     {sku: '100%蚕丝被', xssl: '2000', kcsl: '2400', kcje: '3400', kczb: '46'},
      //     {sku: '100%蚕丝被', xssl: '2000', kcsl: '2400', kcje: '3400', kczb: '46'},
      //     {sku: '100%蚕丝被', xssl: '2200', kcsl: '2400', kcje: '3400', kczb: '46'},
      //     {sku: '100%蚕丝被', xssl: '2000', kcsl: '2400', kcje: '3400', kczb: '46'},
      //     {sku: '100%蚕丝被', xssl: '1600', kcsl: '2400', kcje: '3400', kczb: '46'},
      //     {sku: '100%蚕丝被', xssl: '2000', kcsl: '2400', kcje: '3400', kczb: '46'},
      //     {sku: '100%蚕丝被', xssl: '2400', kcsl: '2400', kcje: '3400', kczb: '46'},
      //     {sku: '100%蚕丝被', xssl: '2000', kcsl: '2400', kcje: '3400', kczb: '46'},
      //     {sku: '100%蚕丝被', xssl: '3200', kcsl: '2400', kcje: '3400', kczb: '46'}
      //   ]
      // }
    }
  },
  computed: {
    dateStr () {
      return this.$store.state.dateStr
    },
    dateStrM () {
      return this.$store.getters.dateStrM
    },
    dateStrJ () {
      return this.$store.getters.dateStrJ
    },
    userIdentityInit () {
      return this.$store.state.userIdentityInit
    },
    selectedDpName () {
      return this.$store.state.selectedDpName
    },
    selectedZgsName () {
      const authList = this.$store.state.authList
      let str = ''
      if (this.userIdentityInit === '2') {
        for (let item of authList) {
          if (item.shop === this.selectedDpName) {
            str = item.role
            break
          }
        }
      } else if (this.userIdentityInit === '3') {
        for (let item of authList) {
          if (item.role === this.selectedDpName) {
            str = item.zgs
            break
          }
        }
      }
      return str
    }
  },
  methods: {
    changeTableOrder ({header, type}) {
      let tempTable = {tableHeader: [], tableContent: []}
      if (type === 'stock-shops-kcfx') {
        tempTable = _.cloneDeep(this.shopKcfxTableData)
      } else if (type === 'stock-shops-jgfx') {
        tempTable = _.cloneDeep(this.shopJgfxTableData)
      }
      const headerList = []
      let tableContent = []
      for (let item of tempTable.tableHeader) {
        if (header.order === '0') {
          // 说明想要升序
          if (item.needOrder && header.prop === item.prop) {
            item.order = '1'
          } else if (item.needOrder && header.prop !== item.prop) {
            item.order = '0'
          }
        } else if (header.order === '1') {
          // 说明之前是升序，想要降序
          if (item.needOrder && header.prop === item.prop) {
            item.order = '2'
          } else if (item.needOrder && header.prop !== item.prop) {
            item.order = '0'
          }
        } else if (header.order === '2') {
          // 说明之前是降序，想要升序
          if (item.needOrder && header.prop === item.prop) {
            item.order = '1'
          } else if (item.needOrder && header.prop !== item.prop) {
            item.order = '0'
          }
        }
        headerList.push(item)
      }
      if (header.order === '0') {
        // 说明想要升序
        tableContent = tempTable.tableContent.sort(compareSort(header.prop, true))
      } else if (header.order === '1') {
        // 说明之前是升序，想要降序
        tableContent = tempTable.tableContent.sort(compareSort(header.prop, false))
      } else if (header.order === '2') {
        // 说明之前是降序，想要升序
        tableContent = tempTable.tableContent.sort(compareSort(header.prop, true))
      }
      if (type === 'stock-shops-kcfx') {
        this.shopKcfxTableData.tableHeader = headerList
        this.shopKcfxTableData.tableContent = tableContent
      } else if (type === 'stock-shops-jgfx') {
        this.shopJgfxTableData.tableHeader = headerList
        this.shopJgfxTableData.tableContent = tableContent
      }
    },
    changeTableRow ({row, name, type}) {
      // 当点击了表格的某一行，进行联动操作
      this.shopQsfxTitle = '趋势分析-' + name
      this.shopJgfxTitle = '结构分析-' + name
      this.shopXsfxTitle = '有库存无销售分析-' + name
    },
    changeJgfxType ({activeTypeName, index, type}) {
      if (type === 'stock-shops-jgfx') {
        this.shopJgfxTabIndex = index
        this.dealShopJgfxData()
      }
    },
    tabsType ({activeValue, index, type}) {
      if (type === 'stock-shops-jgfx') {
        this.shopJgfxBtnIndex = index
        this.dealShopJgfxData()
      }
    },
    getQsfxLineOption (arr) {
      const option = {
        'legend': {
          show: true,
          textStyle: {
            fontFamily: 'PingFangSC-Regular',
            color: '#71768C',
            fontSize: px2px(24)
          }
        },
        'xAxis': [
          {
            'type': 'category',
            'name': '',
            interval: 0,
            nameLocation: 'start',
            'nameTextStyle': {
              'color': '#71768C',
              fontSize: px2px(24),
              padding: [0, 0, px2px(-30), px2px(-80)]
            },
            'data': [],
            'axisPointer': {
              'type': 'shadow'
            },
            'axisLabel': {
              'show': true,
              interval: 0,
              'textStyle': {
                'color': '#71768C'
              },
              fontSize: px2px(24)
            },
            'axisLine': {
              'show': false
            },
            axisTick: {
              'show': false
            }
          }
        ],
        'yAxis': [
          {
            'type': 'value',
            'name': '(天)',
            'nameTextStyle': {
              'color': '#71768C',
              fontSize: px2px(24)
            },
            'axisLabel': {
              'show': true,
              fontSize: px2px(24),
              'textStyle': {
                'color': '#71768C'
              }
            },
            'axisLine': {
              show: false,
              lineStyle: {
                color: '#ECEFF2'
              }
            },
            'splitLine': {
              show: false,
              'lineStyle': {
                'color': '#7d838b'
              }
            },
            axisTick: {
              'show': false
            }
          },
          {
            'type': 'value',
            'name': '控制差异率',
            'nameTextStyle': {
              'color': '#71768C',
              fontSize: px2px(24)
            },
            'show': true,
            'axisLabel': {
              show: true,
              fontSize: px2px(24),
              'textStyle': {
                'color': '#71768C'
              },
              formatter: '{value}%'
            },
            'axisLine': {
              show: false,
              lineStyle: {
                color: '#ECEFF2'
              }
            },
            'splitLine': {
              show: true,
              lineStyle: {
                color: '#213A6B'
              }
            }
          }
        ],
        series: [
          {
            type: 'bar',
            name: '周转天数',
            barWidth: px2px(40),
            itemStyle: {
              barBorderRadius: [px2px(20), px2px(20), 0, 0],
              color: function (params) {
                return '#326DFF'
              }
            },
            data: []
          },
          {
            type: 'line',
            name: '控制差异率',
            smooth: true,
            symbol: 'none',
            yAxisIndex: 1,
            itemStyle: {
              color: '#FFBE14' // 图例的颜色
            },
            lineStyle: {
              color: '#FFBE14',
              type: 'solid',
              width: px2px(4)
            },
            data: []
          },
          {
            type: 'line',
            name: '差异率',
            yAxisIndex: 1,
            symbol: 'none',
            itemStyle: {
              color: '#FF0200' // 图例的颜色
            },
            lineStyle: {
              color: '#FF0200',
              type: 'dashed',
              width: px2px(4)
            },
            data: []
          }]
      }
      const labelList = []
      const zztsList = []
      const zzcyList = []
      const cyList = []
      for (let item of arr) {
        labelList.push(item.label)
        zztsList.push(item.zzts)
        zzcyList.push(item.kzcy)
        cyList.push(item.cy)
      }
      option.xAxis[0].data = labelList
      option.series[0].data = zztsList
      option.series[1].data = zzcyList
      option.series[2].data = cyList
      return option
    },
    getJgfxBarLineOption (arr) {
      const option = {
        'legend': {
          show: true,
          top: px2px(20),
          textStyle: {
            fontFamily: 'PingFangSC-Regular',
            color: '#71768C',
            fontSize: px2px(24)
          },
          data: ['库存占比', '销售占比']
        },
        grid: {
          top: px2px(60),
          left: ((this.showZy === true && this.zyJgfxTabIndex === 0) || (this.showZy === false && this.zgsJgfxTabIndex === 1)) ? px2px(100) : px2px(200)
        },
        'xAxis': [
          {
            'type': 'value',
            'name': '',
            interval: 0,
            'show': false,
            nameLocation: 'start',
            'nameTextStyle': {
              'color': '#71768C',
              fontSize: px2px(24),
              padding: [0, 0, px2px(-30), px2px(-80)]
            },
            'data': [],
            'axisPointer': {
              'type': 'shadow'
            },
            'axisLabel': {
              'show': true,
              interval: 0,
              'textStyle': {
                'color': '#71768C'
              },
              fontSize: px2px(24)
            },
            'axisLine': {
              'show': false
            },
            axisTick: {
              'show': false
            }
          }
        ],
        'yAxis': [
          {
            'type': 'category',
            'axisLine': {
              show: false,
              lineStyle: {
                'color': '#ECEFF2'
              }
            },
            axisLabel: {
              show: true,
              textStyle: {
                'color': '#5F6D96',
                fontSize: px2px(28)
              },
              formatter: function (params) {
                let str = ''
                if (params.length > 8) {
                  str = params.substring(0, 8) + '\n' + params.substring(8)
                } else {
                  str = params
                }
                return str
              }
            },
            'splitLine': {
              show: false,
              'lineStyle': {
                'color': '#7d838b'
              }
            },
            axisTick: {
              'show': false
            }
          }
        ],
        series: [
          {
            type: 'bar',
            name: '库存占比',
            barWidth: px2px(32),
            barGap: 0,
            label: {
              normal: {
                position: 'right',
                formatter: '{c}%',
                'show': true,
                fontSize: px2px(24),
                'color': '#326DFF'
              }
            },
            itemStyle: {
              barBorderRadius: [0, px2px(20), px2px(20), 0],
              color: function (params) {
                return '#326DFF'
              }
            },
            data: []
          },
          {
            type: 'bar',
            name: '销售占比',
            barWidth: px2px(32),
            barGap: 0,
            label: {
              normal: {
                position: 'right',
                formatter: '{c}%',
                show: true,
                fontSize: px2px(24),
                'color': '#D1A226'
              }
            },
            itemStyle: {
              barBorderRadius: [0, px2px(20), px2px(20), 0],
              color: function (params) {
                return '#D1A226'
              }
            },
            data: []
          }]
      }
      const labelList = []
      const kczbList = []
      const xszbList = []
      let i = 0
      for (let item of arr) {
        i++
        labelList.unshift(item.type)
        kczbList.unshift(item.kb)
        xszbList.unshift(item.xb)
        if (i === 10) {
          break
        }
      }
      option.yAxis[0].data = labelList
      option.series[0].data = kczbList
      option.series[1].data = xszbList
      return option
    },
    getAllData () {
      this.getShopKcfxTableData()
      this.shopQsfxTitle = '趋势分析-' + this.selectedDpName
      this.shopJgfxTitle = '结构分析-' + this.selectedDpName
      this.getShopQsfxLineData(this.selectedDpName)
      this.getShopJgfxBarLineData(this.selectedDpName)
    },
    // 直营子公司库存分析的数据
    async getShopKcfxTableData () {
      let param = {
        examSub: '直营',
        subName: this.selectedZgsName,
        dateStrM: this.dateStrM
      }
      const {__statusCode, data} = await apiService.getDataFromInterface(apiService.stockShop.getShopKcfxTableData,
        param)
      if (Object.is(__statusCode, '1')) {
        const result = JSON.parse(data)
        this.shopKcfxTableDataInit = result
        this.dealshopKcfxTableData()
      }
    },
    dealshopKcfxTableData () {
      const list = []
      let dataList = this.shopKcfxTableDataInit.res
      for (let item of dataList) {
        const obj = {}
        obj.shop = item.customer_name
        obj.kzcy = switchPercent(item.diff_rate)
        obj.zzts = formatNumber(initNum(item.zz_rate), 2)
        obj.kzcypm = formatNumber(initNum(item.order_diff_rate), 0)
        obj.zztspm = formatNumber(initNum(item.order_zz_rate), 0)
        list.push(obj)
      }
      if (list.length > 0) {
        this.shopKcfxTableData.tableContent = list.sort(compareSort('kzcypm', true))
      } else {
        this.shopKcfxTableData.tableContent = []
      }
    },
    // 获取店铺，趋势分析的线图数据
    async getShopQsfxLineData (shopName) {
      let params = {
        examSub: '直营',
        cusName: shopName,
        dateStrM: this.dateStrM
      }
      const {__statusCode, data} = await apiService.getDataFromInterface(apiService.stockShop.getShopQsfxBarLineData, params)
      if (Object.is(__statusCode, '1')) {
        const result = JSON.parse(data)
        const list = []
        for (let item of result.res) {
          const obj = {}
          obj.label = item.period_wid
          obj.kzcy = (initNum(item.diff_rate) * 100).toFixed(0)
          obj.zzts = formatNumber(initNum(item.zz_rate), 2)
          obj.cy = (initNum(item.target_diff_rate) * 100).toFixed(0)
          list.push(obj)
        }
        if (list.length > 0) {
          this.shopQsfxLineData = list
        } else {
          this.shopQsfxLineData = []
        }
      }
    },
    async getShopJgfxBarLineData (shopName) {
      let params = {
        examSub: '直营',
        cusName: shopName,
        dateStrM: this.dateStrM
      }
      const {__statusCode, data} = await apiService.getDataFromInterface(apiService.stockShop.getShopJgfxBarLineData, params)
      if (Object.is(__statusCode, '1')) {
        const result = JSON.parse(data)
        this.shopJgfxTableDataInit = result
        this.dealShopJgfxData()
      }
    },
    dealShopJgfxData () {
      this.shopJgfxIndex = this.shopJgfxTabIndex.toString() + this.shopJgfxBtnIndex.toString()
      const BarLineList = []
      const list = []
      let dataList = []
      if (this.shopJgfxIndex === '00') {
        dataList = this.shopJgfxTableDataInit.resDQ || []
        this.shopJgfxTableData.tableHeader = this.dqTableHeader
      } else if (this.shopJgfxIndex === '01') {
        dataList = this.shopJgfxTableDataInit.resDM || []
        this.shopJgfxTableData.tableHeader = this.dmTableHeader
      } else if (this.shopJgfxIndex === '10') {
        dataList = this.shopJgfxTableDataInit.resSPUQ || []
        this.shopJgfxTableData.tableHeader = this.spuqTableHeader
      } else if (this.shopJgfxIndex === '11') {
        dataList = this.shopJgfxTableDataInit.resSPUM || []
        this.shopJgfxTableData.tableHeader = this.spumTableHeader
      }
      for (let item of dataList) {
        const obj = {}
        obj.type = item.type_name
        obj.xe = formatNumber(initNum(item.sale), 2)
        obj.ke = formatNumber(initNum(item.inv), 2)
        obj.xb = switchPercent(item.sale_rotio_rate)
        obj.kb = switchPercent(item.inv_rotio_rate)
        obj.pc = switchPercent(item.deviation)
        list.push(obj)
        BarLineList.push(obj)
      }
      this.shopJgfxBarLineData = BarLineList.sort(compareSort('kb', false))
      this.shopJgfxTableData.tableContent = list.sort(compareSort('ke', false))
    }
  },
  created () {
    this.$store.commit('updateHeaderTitle', '库存')
    this.$store.commit('updateShowIcon', true)
    this.$store.commit('updateDateStr', this.$store.state.dateStrInit)
    this.$store.dispatch('updateUserOption', {USER_ACCOUNT: window.userAccount, EXAMINATION_SUBJECT: '直营', LEVEL: '店铺', URL: '库存'})
    if (this.dateStr !== '') {
      this.getAllData()
    }
  },
  beforeDestroy () {
    this.$eventBus.$off('userSelected', this.getAllData)
  },
  mounted () {
    this.$eventBus.$on('userSelected', this.getAllData)
  },
  components: {
    pageTitle,
    MinTitle,
    BarLineChart,
    TableList,
    TypeList,
    Tabs
  }
}
</script>

